@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movieRow {
    margin-bottom: 3rem;
}
.movieRow h2 {
    font-size: 4rem;
    margin: 0 0 0 3rem;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 3rem;
}
.movieRow--list {
    transition:all 0.4s ease-in-out;
    -webkit-transition:all 0.4s ease-in-out;
    -moz-transition:all 0.4s ease-in-out;
    -ms-transition:all 0.4s ease-in-out;
    -o-transition:all 0.4s ease-in-out;
}

.movieRow--movie {
    width: 15rem;
    display: inline-block;
    cursor: pointer;
    overflow-y: hidden;
}

.movieRow--movie img{
    width:100%;
    max-height: 22.5rem;
    min-height: 22.5rem;


    transform: scale(0.9);
    -webkit-transform: scale(0.9);
    -moz-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -o-transform: scale(0.9);
    transition: 0.2s ease-in-out;
    -webkit-transition: 0.2s ease-in-out;
    -moz-transition: 0.2s ease-in-out;
    -ms-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
}
.movieRow--movie img:hover{
    width:100%;
    transform: scale(1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
}
.movieRow--left,
.movieRow--right {
    position:absolute;
    width: 4rem;
    height: 22.5rem;
    background-color:rgba(0,0,0,0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
    -webkit-transition: opacity 0.2s ease-in-out;
    -moz-transition: opacity 0.2s ease-in-out;
    -ms-transition: opacity 0.2s ease-in-out;
    -o-transition: opacity 0.2s ease-in-out;
    overflow: hidden;
}
.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
    opacity: 1;
}
.movieRow--left {
    left: 0;
}
.movieRow--right {
    right: 0;
}

@media screen and (max-width: 768px) { 
    .movieRow--left,
    .movieRow--right {
        opacity: 1;
    }
    .movieRow h2 {
        font-size: 2rem;
        margin: 0 0 0 3rem;
    }
    
}
.Destaque {
    height: 100vh;
}
.Destaque--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%,transparent 90%);
}
.Destaque--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%,transparent 70%);
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-left:3rem;
    padding-bottom: 15rem;
    padding-top: 7rem;
    
}

.Destaque--name {
    font-size: 6rem;
    font-weight: bold;
}

.Destaque--info {
    font-size: 1.8rem;
    font-weight: bold;
    margin-top:2.5rem;
}

.Destaque--points,.Destaque--year, .Destaque--seasons {
    display: inline-block;
    margin-right:2.5rem;
    margin-bottom: 2.5rem;
}
.Destaque--points {
    color: #46d369;
}
.Destaque--description {
    font-size: 2rem;
    color: #999;
    max-width: 40%;
}
.Destaque--buttons {
    margin-top: 2.5rem;
}

.Destaque--btn {
    display: inline-block;
    font-size:2rem;
    font-weight: bold;
    padding: 1.2rem 2.5rem;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    text-decoration: none;
    margin-right: 1rem;
    opacity: 1;
    transition: opacity 0.2s ease-in-out;
    -webkit-transition: opacity 0.2s ease-in-out;
    -moz-transition: opacity 0.2s ease-in-out;
    -ms-transition: opacity 0.2s ease-in-out;
    -o-transition: opacity 0.2s ease-in-out;
}
.Destaque--btn:hover {
    opacity: .6;
}

.watchBtn {
    background-color: white;
    color: #111;
}
.listBtn {
    background-color: #333;
    color: white;
}
.Destaque--genres {
    margin-top: 2.5rem;
    font-size: 1.8rem;
    color: #999;
}

@media screen and (max-width:768px) {
    .Destaque {
        height: 90vh;
    }
    .Destaque--name {
        font-size: 3rem;
    }
    .Destaque--info {
        font-size: 1.6rem;
    }
    .Destaque--description {
        font-size: 1.4rem;
        max-width: 100%;
        margin-right: 3rem;
    }
    .Destaque--btn {
        font-size: 1.6rem;
    }
    .Destaque--genres {
        font-size: 1.4rem;
    }

}
@media screen and (max-width:320px) {
    .Destaque--buttons {
        padding: 0 3rem 0 0;
    }
    .Destaque--btn {
        width: 100%;
        margin-right: 0;
        margin-top: 1rem;
        font-size: 1.4rem;
    }
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;

}

html {
  font-size: 62.5%;
}

body {
  background-color: #111;
  color: #fff;
  
  font-family: 'Roboto', sans-serif;
}

.list {
  margin-top: -15rem;
}

footer {
  margin-top: 5rem;
  text-align: center;
  background: #14141414;
  padding:3rem 0;
  font-size: 2rem;
}
footer a{
  text-decoration: none;
  background: linear-gradient(to right, #fef5c0 0%, #d5391e 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: #0000;
}

footer a+a{
  text-decoration: none;
  background: linear-gradient(to right, #c0fecf 0%, #1ed5a9 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: #0000;
}

.loading{
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
}
header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 7rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 3rem;
    background: transparent;
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
}

header.DarkHeader {
    background: #141414f3;
}

.header--logo {
    height: 4rem;
}
.header--user{
    height: 5rem;
}
.header--logo img,.header--user img {
    height: 100%;
    border-radius: .5rem;
}

@media screen and (max-width: 768px) {
    .header--logo {
        height: 2.5rem;
    }
    .header--user{
        height: 3rem;
    }
}
